    /*darcula colour scheme*/
    --background: #282a36;
    --current-line: #44475a;
    --foreground: #f8f8f2;
    --comment: #6272a4;
    --cyan: #8be9fd;
    --green: #50fa7b;
    --orange: #ffb86c;
    --pink: #ff79c6;
    --purple: #bd93f9;
    --red: #ff5555;
    --yellow: #f1fa8c;
    --context-menu-background: #44475a;
    --highlighted-tree: #52566c;
    --input-outline: #14141a;
    --disabled-input: #3c3f4e;
    --disabled-input-text: #b6b7b6;

    --value-colour-on: #50fa7b;
    --value-type-colour-on: #f1fa8c;
    --value-colour-off: var(--foreground);
    --value-type-colour-off: #c3c5c7;
    --value-colour: var(--value-colour-on);
    --value-type-colour: var(--value-type-colour-on);

    --highlighted-input: #0d3806;
    --highlighted-input-text: #f8f8f2;
}

body {
    background-color: var(--background);
    color: var(--foreground);
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    width: 98%;
    height: 98vh;
    margin: 1vh 1vw;
}

h3 {
    margin-top: 0;
}

h4 {
    margin-top: 0;
    margin-bottom: 0;
}

input {
    background-color: var(--current-line);
    color: var(--foreground);
    border: var(--input-outline) solid 1px;
    border-radius: 3px;
    padding: 0 2px;
    font-family: 'Roboto Mono', sans-serif;
}

input:disabled {
    background-color: var(--disabled-input);
    color: var(--disabled-input-text);
    cursor: not-allowed;
}

button {
    background-color: var(--current-line);
    color: var(--foreground);
    border: var(--input-outline) solid 2px;
    border-radius: 3px;
    padding: 2px;
    font-family: 'Roboto', sans-serif;
}

button:disabled {
    background-color: var(--disabled-input);
    color: var(--disabled-input-text);
}

button:active {
    background-color: var(--highlighted-tree);
}

button:not(:disabled) {
    cursor: pointer;
}

select {
    background-color: var(--current-line);
    color: var(--foreground);
    border: var(--input-outline) solid 1px;
    border-radius: 3px;
    padding: 0 2px;
    font-family: 'Roboto', sans-serif;
}

select:disabled {
    cursor: not-allowed;
    background-color: var(--disabled-input);
    color: var(--disabled-input-text);
    opacity: 0.6;
}

form {
    display: inline;
}

#controls {
    display: flex;
    margin-bottom: 10px;
}

#controls > div {
    margin: 0 10px 0 0;
    border: 2px solid var(--input-outline);
    border-radius: 2px;
    padding: 2px;
}

#tree-buttons {
    margin: 3px;
    z-index: 100;
    position: relative;
    display: inline-block;
}

#tree-buttons button {
    padding: 0;
}

#tree-buttons button img {
    vertical-align: middle;
}

#tree-container {
    border: var(--foreground) solid 1px;
    width: 100%;
    height: 100%;
    min-height: 100px;
    overflow: hidden;
}

#tree-container.file-drag-highlight {
    border: 2px dashed #4CAF50;
    background-color: #49494d;
}

#tree {
    /*margin-top: 30px;*/
    max-width: fit-content;
    overflow: visible;
    white-space: nowrap;
}

#tree, .subtree {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    align-self: flex-end;
    position: relative;
    border-radius: 3px;
}

.highlight {
    background-color: var(--highlighted-tree);
}

.args {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.node {
    display: inline-flex;
    margin-top: 0.5em;
    align-items: center;
    justify-content: space-between;
}

.node div:not(.expr-selector-container) {
    display: inline;
}

div.scoped-variables {
    display: inline-flex;
}

.annotation-axiom {
    align-self: end;
    font-size: 12px;
    color: var(--comment);
}

.annotation-new {
    font-size: 12px;
    color: var(--comment);
    align-self: flex-end;
}

.axiom {
    display: flex;
    flex-direction: column-reverse;
}

.subtree:not(:last-child) {
    margin-right: 20px;
}

.subtree::before {
    content: '';
    height: 2px;
    background: var(--foreground);
    width: 100%;
    position: absolute;
    /*bottom: 27px;*/
    bottom: 1.4em;
    left: 50%;
    transform: translate(-50%, -50%);
}

.custom-menu {
    display: none;
    position: absolute;
    box-shadow: 1px 1px 2px #888;
    border: solid 1px #ccc;
    background-color: var(--context-menu-background);
    padding: 2px;
    z-index: 1000;
}

#custom-context-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

#custom-context-menu ul li {
    padding: 10px;
    cursor: pointer;
    text-align: left;
}

#custom-context-menu ul li:hover {
    background-color: var(--highlighted-tree);
}

.tooltip:not(.tooltip .tooltip) {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted var(--foreground);
}

.tooltip .tooltip-text {
    visibility: hidden;
    background-color: var(--context-menu-background);
    color: var(--foreground);
    border: var(--input-outline) solid 1px;
    text-align: center;
    border-radius: 6px;
    padding: 5px 3px;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    transition: opacity 0.3s;
}

.tooltip:hover .tooltip-text {
    visibility: visible;
    opacity: 1;
}

.tooltip div {
    display: inline;
}

.value {
    color: var(--value-colour);
}

.value-type {
    color: var(--value-type-colour);
}

.error-origin {
    color: var(--red);
    font-family: 'Roboto Mono', sans-serif;
}

.error-child {
    color: var(--orange);
}

.error-message {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: var(--red);
    color: var(--background);
    padding: 15px;
    border-radius: 5px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s, visibility 0.5s;
}

.error-message.fade-in {
    opacity: 1;
    visibility: visible;
}

.error-message.fade-out {
    opacity: 0;
    visibility: hidden;
}

div.expr-selector-container {
    display: inline-flex;
    position: relative;
}

div.expr-selector-container.guide-highlight * {
    background-color: var(--highlighted-input);
    color: var(--highlighted-input-text);
}

.expr-selector-input {
    margin: 0;
    border-radius: 3px 0 0 3px;
}

.expr-selector-button {
    margin: 0;
    border: var(--input-outline) solid 1px;
    border-radius: 0 3px 3px 0;
}

.expr-selector-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: var(--context-menu-background);
    color: var(--foreground);
    border: var(--input-outline) solid 1px;
    z-index: 1;
}

.expr-selector-dropdown > ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.expr-selector-option {
    cursor: pointer;
    padding: 5px;
    text-align: left;
    z-index: 100;
}

.expr-selector-option.highlight {
    background-color: var(--highlighted-tree);
}

label, .prevent-select, #tree :not(input) {
    user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}
